h1 {
    text-align: center;
    margin: 50px;
    justify-content: center;
    align-items: center;
    display: flex;
}

.block-example {
    width: 6rem;
    height: 6rem;
    margin: 0.5rem;
    background-color: black;
    display: inline-block;
}

h2 {
    color: grey;
    text-align: center;
}

h3 {
    color: white;
    text-align: center;
    align-items: center;
    padding-bottom: 20px;
}

h3:hover {
    color: black;
}

.meetup {
    padding-top: 50px;
}

.inputBoxText {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}
